/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
    @include edgtfRelativeHolderLayout();

	&.edgtf-info-on-image {

		.edgtf-pli {
			.edgtf-pli-inner { 
				.edgtf-pli-text { 
					.button { 
						width:auto;
					}
				}
			}

			.edgtf-pli-add-to-cart {
				margin: 22px 0 0;
			}

			.edgtf-pli-price {
				margin: 9px 0 0;
			}
		}
	}

	&.edgtf-info-below-image {

		.edgtf-pli {

			.edgtf-pli-inner {

				.edgtf-pli-text {
					padding: 0;
					overflow:hidden;
					background-color: transparent;

					.edgtf-pli-add-to-cart {
						margin: 0;
					}

					.button {
						width: 100%;
						text-align: center;
						@include edgtfTransform(translate3d(0,100%,0));
						@include edgtfTransition(all .25s cubic-bezier(.5,.15,.33,.88));

						&:not(.edgtf-btn-huge) .edgtf-btn-text {
							float: none;

							+ i {
								float: none;
							}
						}

						&:hover {

							&:after {
								box-shadow: 3px 4px 5px 0 rgba(0, 0, 0, .1);
							}
						}
					}
				}
			}

			.edgtf-pli-text-wrapper {
				.edgtf-pli-text-wrapper-inner {
					float: left;
				}

				.edgtf-pli-price {
					color: $second-heading-color;
					margin: 0;
					float: right;
				}

				.edgtf-pli-category {
					margin: 5px 0 3px;
				}
			}

			&:hover {
				.edgtf-pli-inner .edgtf-pli-text .button {
					@include edgtfTransform(translate3d(0,0%,0));
				}
			}
		}
	}

    .edgtf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

	    .edgtf-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .edgtf-pli-category {
		    margin: 11px 0 3px;

			a {
				font-size: 15px;
				font-family: $third-heading-font;
				font-style: italic;
				color: #9c9c9c;

				&:hover {
					color: $first-main-color;
				}
			}
	    }

        .edgtf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .edgtf-pli-rating-holder {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .edgtf-pli-rating {
		    @include edgtfWooCommerceRatingsStyle();
	    }

        .edgtf-pli-price {
	        @include edgtfWooCommercePriceStyle();
        }
    }

    .edgtf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: visible;

        &:hover {

            .edgtf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .edgtf-pli-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-pli-onsale,
            .edgtf-pli-out-of-stock,
            .edgtf-pli-new-product {
	            @include edgtfWooCommerceMarksGeneralStyle();
            }

            .edgtf-pli-onsale {
	            @include edgtfWooCommerceSaleStyle();
            }

            .edgtf-pli-out-of-stock {
	            @include edgtfWooCommerceOutOfStockStyle();
            }
	
	        .edgtf-pli-new-product {
		        @include edgtfWooCommerceNewProductStyle();
	        }
        }
            
        .edgtf-pli-text {
            @include edgtfAbsoluteHolderLayout();
			padding: 30px;
            opacity: 0;
            background-color: rgba(#fff, 0.93);
            box-sizing: border-box;
            @include edgtfTransition(opacity .2s ease-out);
        }

        .edgtf-pli-text-outer {
	        @include edgtfTableLayout();
	        text-align: left;
        }

        .edgtf-pli-text-inner {
            @include edgtfTableCellLayout();
			vertical-align: bottom;

            .edgtf-pli-title,
            .edgtf-pli-category a,
            .edgtf-pli-excerpt,
            .edgtf-pli-rating {
				color: #231f20;
            }

			.edgtf-pli-price {
				color: $first-main-color;
			}
	
	        .edgtf-pli-category a:hover {
		        color: rgba(#231f20, .8);
	        }

	        .edgtf-pli-add-to-cart {

		        &.edgtf-default-skin {

			        .button,
			        .added_to_cart {
				        @include edgtfWooButtonSolidColor();
				        text-align:center;

				        &:hover {
				            @include edgtfWooButtonSolidColor();
				        }
			        }
		        }

		        &.edgtf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: #fff;
				        }
			        }
		        }
		
		        &.edgtf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $default-heading-color;
				        }
			        }
		        }

		        &:hover {
		        	.edgtf-button-overlay-holder .edgtf-button-overlay { 
		        		@include edgtfTransform(translate(-50%,-50%) scale(1));
		        	}
		        }
	        }

            .button,
            .added_to_cart {
	            @include edgtfButtonDefaultStyle();
	            @include edgtfButtonSmallParams();
				@include edgtfWooButtonAdditionalStyles();
	            white-space: nowrap;
	            z-index: 3;

	            .edgtf-btn-text {
	            	position: relative;
    				z-index: 10;
	            }
            }

            .button.added {
                display: none;
            }

	        .edgtf-button-overlay-holder {
	            position: absolute !important;
			    bottom: -1px;
			    left: -1px;
			    width: calc(100% + 2px);
			    height: calc(100% + 2px);
			    overflow: hidden;
			    z-index:3;

	            .edgtf-button-overlay {
	                position:absolute;
	                border-radius:50%;
	                z-index:3;
	                width:1000px;
	                height:1000px;
	                background-color: $third-main-color;
	                transform-origin:center;
	                @include edgtfTransitionTransform(.35s cubic-bezier(.5,.15,.33,.88));
	                @include edgtfTransform(translate(-50%,-50%) scale(0));
	            }

	        }
        }

	    .edgtf-pli-link {
		    @include edgtfAbsoluteHolderLayout();
	    }
    }

    .edgtf-pli-text-wrapper {
        @include edgtfRelativeHolderLayout();
        margin: 22px 0 38px;
        text-align: left;
    }
	
	&.edgtf-product-info-light {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: #fff;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.edgtf-product-info-dark {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: $default-heading-color;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.edgtf-standard-layout {
		
		.edgtf-pli {
			float: left;
		}
		
		&.edgtf-large-space {
			margin: 0 0 -50px;
		}
		
		&.edgtf-normal-space {
			margin: 0 0 -30px;
		}
		
		&.edgtf-small-space {
			margin: 0 0 -20px;
		}
		
		&.edgtf-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.edgtf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.edgtf-pli {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.edgtf-masonry-layout {
		
		.edgtf-pl-outer {
			opacity: 0;
		}
		
		.edgtf-pl-sizer {
			width: 100%;
		}
		
		.edgtf-pl-gutter {
			width: 0;
		}
		
		.edgtf-pli-inner {
			width: 100%;
			height: 100%;
			
			.edgtf-pli-image {
				height: 100%;
				
				img {
					height: 100%;
				}
			}
		}
		
		.edgtf-pli.edgtf-woo-image-large-width {
			
			img {
				width: 100%;
			}
		}
		
		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.edgtf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.edgtf-pli,
				.edgtf-pl-sizer {
					width: $column_width;
				}
				
				.edgtf-pli.edgtf-woo-image-large-width,
				.edgtf-pli.edgtf-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */