/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.edgtf-header-cart-icon-pack {
            color: #231f20;

            &:hover {
                color: $first-main-color;
            }
            
            .edgtf-cart-icon {
                font-size: 22px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            
            .edgtf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .edgtf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 291px;
    background-color: #231e2d;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include edgtfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));
    box-shadow: $default-box-shadow;

    ul {
        margin: 37px 20px;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 10px 0;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                font-family: $default-heading-font;
                font-weight: 700;
                letter-spacing: 0.06em;
                text-align: center;
                color: #fff;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 63px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        vertical-align: top;
        padding-left: 22px;

        .edgtf-product-title {
            display: block;
            padding: 13px 20px 0 0;
            margin: 0 0 4px;
            box-sizing: border-box;
            color: #fff;
            font-family: $second-heading-font;
            font-size: 13px;
        }

        .remove {
            position: absolute;
            top: 12px;
            right: -4px;
            font-size: 16px;
            font-weight: bold;
            line-height: 1;
            color: #fff;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity,
        .amount {
            font-family: $second-heading-font;
            font-size: 13px;
            font-weight: 700;
            text-transform: uppercase;
            margin: 3px 0 0;
            color: #9c9c9c;
        }
    }

    .edgtf-cart-bottom {
        padding: 14px 0 0;

        .edgtf-subtotal-holder {
            position: relative;
            display: block;
            width: 100%;
            margin: 0 0 27px;

            > * {
                display: inline-block;
                vertical-align: baseline;
                color: #fff;
                line-height: 20px;
                font-size: 13px;
                font-family: $second-heading-font;
                font-weight: 700;
                text-transform: uppercase;
            }

            .edgtf-total-amount {
	            font-size: 21px;
            }
        }

        .edgtf-view-cart {
            @include edgtfRelativeHolderLayout();
            height: 62px;
            line-height: 62px;
            padding: 0 26px;
            font-family: $default-heading-font;
            font-size: 16px;
            font-weight: 800;
	        letter-spacing: 0;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include edgtfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            i {
                font-size: 11px;
                margin: 0 0 0 3px;
            }

            &:hover {
                color: #fff;
                background-color: $third-main-color;
            }
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */