/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    margin: 0 0 57px;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include edgtfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include edgtfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include edgtfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .edgtf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .edgtf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {

        .edgtf-widget-title-holder .edgtf-widget-title{
            margin: 0 0 32px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #d6d6d7;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 12px;
                    width: 12px;
                    border-radius: 100%;
                    background-color: $second-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include edgtfTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $second-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 35px 0 0;
            
            .button {
                float: left;
            }
            
            .price_label {
                float: right;
                color:$third-heading-color;
                font-style: italic;
                font-size:15px;
                font-family: $third-heading-font;
                height: 42px;
                line-height: 42px;
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 0;
                padding: 3px 0;
                font-family: $third-heading-font;
                font-size: 15px;

                a {
                    color: $third-heading-color;
                    font-style: italic;

                    &:hover {
                        color: $third-main-color;
                    }
                }
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .edgtf-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                    font-weight:700;
                    font-size: 13px;
                    font-family:$second-heading-font;
                    text-transform: uppercase;
                }


                
                .amount {
                    color: $default-heading-color;
                    font-weight:700;
                    font-size: 13px;
                    font-family:$second-heading-font;
                    text-transform: uppercase;
                }
            }
        }
    }

    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {

                margin:-4px 0 0;

                .product-title{
                    margin-top: 11px !important;
                }
                .star-rating {
                    margin-top: -4px;
                    margin-bottom: 3px;
                }
                
                .amount {
                    display: block;
                }
            }

        }
    }
    &.widget_product_tag_cloud {

        .edgtf-widget-title{
            margin: 0 0 14px;
        }
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 15px !important;
                font-family: $third-heading-font;
                color:$third-heading-color;
                font-style: italic;
                margin-right: 4px;
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include edgtfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include edgtfRelativeHolderLayout();
                box-sizing: border-box;
                border-bottom: 3px solid #d6d6d7;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include edgtfTransition(color .2s ease-out);
                font-style: italic;
                color:$third-heading-color;
                font-size: 15px;
                font-family: $third-heading-font;
            }
            
            input {
                width: calc(100% - 40px);
                padding: 16px 12px 10px 16px;
                font-size: 15px;
                letter-spacing: 0.0em;
    
                @include edgtfPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 7px 8px 0 0;
                font-size: 19px;
                cursor: pointer;
                color:$third-heading-color;

                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    &.widget_products{
        ul {
            li{
                margin: 0 0 -4px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */