/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.edgtf-woo-pagination-holder {
    @include edgtfRelativeHolderLayout();
    margin: 0 0 66px;
    clear: both;

    .edgtf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: block;
    vertical-align: middle;
    width: 100%;
    text-align: center;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                margin: 0 6px;
                padding: 0;
                vertical-align: top;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                color: $default-text-color;
                @include edgtfTransition(color .2s ease-out);
                font-family: $third-heading-font;
                font-style: italic;

                &:hover,
                &.current {
                    color: $default-heading-color;
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1;
                    margin: 0 6px 0 4px;

                    &:before {
                        position: relative;
                        top: 2px;
                        display: block;
                        font-family: 'Ionicons';
                        font-size: 18px;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\f3d2";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\f3d3";
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */