/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 30px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: 44px;
    }

        .woocommerce-ordering {
        text-align: right;
        font-family: $third-heading-font;
        color: $third-heading-color;
        
        .orderby {
            width: auto;
	        min-width: 250px;
	        margin: 0;
	        text-align: initial;
        }

        .select2-container--default .select2-selection--single{
            border:1px solid transparent;
            background-color:#f9f9f9;
            height: 64px;
            line-height: 64px;
            font-style: italic;
        }

        .select2-selection__arrow{
            font-style: normal;
            font-size: 11px;
            font-weight: 800;
        }
    }
}
/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */