/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {

    .edgtf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .edgtf-quantity-input,
        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $second-heading-color;
            background-color: transparent;
            text-align: center;
            box-sizing: border-box;
            font-size: 18px;
            font-style: normal;
        }

        .edgtf-quantity-input {
            position: relative;
            height: 58px;
            width: 58px;
            padding: 0;
            margin: 0 29px 0 0;
            font-family: inherit;
            line-height: 58px;
            outline: 0;
            font-family: $second-heading-font;
            font-weight: 600;
            border-color: transparent;
            background-color: #f6f6f6;

            &:focus{
                border:1px solid transparent;
            }
        }

        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            position: absolute;
            right: 0;
            width: 29px;
            height: 29px;
            line-height: 29px;
            border-left: 0;
            cursor: pointer;
            @include edgtfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .edgtf-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .edgtf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .edgtf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        margin: 0 0 14px;
        padding: 12px 20px;
        font-family: inherit;
        font-size: 12px;
        line-height: 18px;
        font-weight: inherit;
        color: $default-text-color;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;


        &:focus {
            border-color: $default-heading-color;
        }
    
        @include edgtfPlaceholder {
            color: inherit;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */