/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edgtf-woo-single-page {
	
	&.edgtf-woo-single-thumb-on-left-side {
		
		.edgtf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include edgtfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .edgtf-single-product-content {
        @include edgtfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 20px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;

			    a {
				    @include edgtfRelativeHolderLayout();

				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 15px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 15px;
				    width: calc(100% - 30px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 30px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .edgtf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .edgtf-single-product-title {
		    display: block;
		    margin: 0 0 8px;
	    }
	
	    .price {
		    @include edgtfWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 3px;

			.woocommerce-review-link{
				color:$third-heading-color;
				font-family: $third-heading-font;
				font-style: italic;
				font-size:15px;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include edgtfRelativeHolderLayout();
		    margin: 37px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }

		.edgtf-sp-info{
			margin: 0 0 16px;
		}
	
	    .product_meta {

		    margin: 0;
		
		    > span {
			    display: block;
			    margin: 0;
				font-family: $default-heading-font;
				color:$second-heading-color;
				text-transform:uppercase;
				font-size:14px;
				font-weight: 700;

			    a {
				    color: $third-heading-color;
					text-transform:none;
					font-family: $third-heading-font;
					font-style: italic;
					font-size: 14px;
					font-weight: 400;

				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .edgtf-woo-social-share-holder {
		    @include edgtfRelativeHolderLayout();
			margin: 43px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
				display: block !important;
		    }

			.edgtf-ps-share-label{
				display: block;
				margin-top: 68px;
				margin-bottom: 13px;
			}

			.edgtf-social-title {
				font-family: $second-heading-font;
				font-weight: 700;
				text-transform: uppercase;
				display: block;
				color: $second-heading-color;
				font-size: 21px;
				margin: 0 0 12px;
			}

			.edgtf-social-share-holder ul li {

				a{
					font-size:14px;
					margin-right: 4px;
				}

			}

	    }

	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include edgtfRelativeHolderLayout();
		    margin: 27px 0 57px;
	    }
	
	    .edgtf-quantity-buttons {
		    margin: 0 34px 0 0;
	    }

		button[type=submit].single_add_to_cart_button:not(.edgtf-woo-search-widget-button) {
			padding: 18px 28px 18px;
			margin: 1px 0 0;
		}
    }
	
	.woocommerce-tabs {
		@include edgtfRelativeHolderLayout();
		margin: 30px 0 0;
		
		ul.tabs {
			@include edgtfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 3px solid #f3f3f3;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 10px 40px;
					font-size: 17px;
					font-weight: 600;
					font-family: $second-heading-font;
					color:$second-heading-color;
					text-transform: uppercase;

					&:hover{
						border-bottom: 3px solid $second-main-color;
						margin-bottom: -3px;
					}

				}

				&.active{
					a {
						border-bottom: 3px solid $second-main-color;
						margin-bottom: -3px;
					}
				}
			}
		}
		
		.entry-content {
			@include edgtfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 18px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include edgtfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include edgtfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include edgtfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 12px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 29px;
			text-align: left;
			font-size: 29px;
		}
		
		.product {
			
			.edgtf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */